import './App.css';
import { utils } from 'ethers';
import { Card, Row, Col, InputNumber, Button, Modal, message, notification } from 'antd';
import * as Wallet from './lib/wallet';
import React, { Component } from 'react';
/// ID
const CHAIN_ID = '0x1';
// const CHAIN_ID = '0x13881';




class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lptBalance: '0',
      stakeBalance: '0',
      lptBtnLoading: false,
      stakeBtnLoading: false,
      rewardBtnLoading: false,
      exitBtnLoading: false,
      approved: true,
      stakeNum: '',
      rewardEarned:'0',
    };

  }

  componentWillMount() {
    this.init();
    Wallet.getEthereum().on('accountsChanged', () => {
      this.init();
    });
    Wallet.getEthereum().on('chainChanged', () => {
      this.init();
    });


  }

  async init() {
    const chainId = await Wallet.getChainId();
    if (chainId !== CHAIN_ID) {
      notification.open({
        description:
          ' Ethereum ',
      });
      return;
    }
    const isConnect = await Wallet.check();
    if (!isConnect) {

      Modal.warning({
        content: 'metamask',
        confirmLoading: true,
        onOk: () => {
          return new Promise(async (res) => {
            const result = await Wallet.connect();
            res();
            if (result) {
              this.initData();
            } else {
              this.init();
            }

          })
        }
      });
      return;
    }

    this.initData();
  }

  async initData() {
    const loading = Wallet.loading();
    const data = {
      lptBalance: await Wallet.getLptBalance(),
      stakeBalance: await Wallet.getStakeBalance(),
      approved: await Wallet.testAllowanceLptNum(),
      stakeNum: '',
      unstakeNum: '',
      rewardEarned:await Wallet.getRewardEarned(),
    }
    console.log(data)
    this.setState(data);
    loading.close();
  }

  render() {

    return (
      <div className="App">
        <div className='title'>QMT</div>
        <div>
          <p className='sub-title'></p>
          <p className='step-text'>1</p>
          <p className='step-text'>2QMT</p>
          <p className='step-text'>3</p>
        </div>
        <div>
          <p className='sub-title'></p>
          <p className='mk-text'>QMT</p>
          <p className='mk-text'></p>
          <p className='mk-text'></p>
        </div>
        <div className='card-box'>
          <Row gutter={20}>
            <Col span={6}>
              <Card title={"QMT" + parseFloat(this.state.lptBalance).toFixed(2)} >
                <InputNumber controls={false} size="large" placeholder="0.0" value={this.state.stakeNum} precision={0} min={0} max={parseFloat(this.state.lptBalance)}
                  onFocus={async (e) => {
                    this.setState({
                      approved: await Wallet.testAllowanceLptNum(),
                    })
                  }}
                  onChange={(e) => {

                    if (!!e && utils.parseEther(e.toString()).gte(utils.parseEther(this.state.lptBalance))) {
                      e = parseFloat(this.state.lptBalance);
                    }
                    this.setState({
                      stakeNum: e
                    })
                  }} suffix="RMB" style={{ width: '100%' }} addonAfter={<span className='input-btn' onClick={() => {
                    this.setState({
                      stakeNum: this.state.lptBalance
                    })
                  }}>MAX</span>} />
                <br></br>
                <br></br>
                <Button loading={this.state.lptBtnLoading} disabled={!parseFloat(this.state.stakeNum) && this.state.approved} size="large" type="primary" style={{ width: '100%' }} onClick={async () => {
                  const loading = Wallet.loading();
                  this.setState({
                    lptBtnLoading: true
                  });

                  const closeLoading = () => {
                    this.setState({
                      lptBtnLoading: false
                    });
                    loading.close();
                  }


                  if (!this.state.approved) {
                    await Wallet.modal({
                      content: "Would you like Approve",
                      okText: 'approve',
                    });

                    const result = await Wallet.approveLptNum(() => {
                      this.setState({
                        approved: true,
                      })
                    });
                    this.setState({
                      approved: result,
                    });

                    closeLoading();
                    return;
                  }

                  const result = await Wallet.stakeLptNum(this.state.stakeNum, () => {
                    message.success('');
                    this.initData();
                  });
                  if (!result) {
                    closeLoading();
                    return;
                  }

                  this.initData();
                  closeLoading();


                }}>{this.state.approved ? '' : ''}</Button>
              </Card>
            </Col>
            <Col span={6}>
              <Card title={"QMT" + parseFloat(this.state.stakeBalance).toFixed(2)} >
                <InputNumber controls={false} size="large" placeholder="0.0" value={this.state.unstakeNum} precision={0} min={0} max={parseFloat(this.state.stakeBalance)} onChange={(e) => {

                  if (!!e && utils.parseEther(e.toString()).gte(utils.parseEther(this.state.stakeBalance))) {
                    e = parseFloat(this.state.stakeBalance);
                  }
                  this.setState({
                    unstakeNum: e
                  })
                }} suffix="RMB" style={{ width: '100%' }} addonAfter={<span className='input-btn' onClick={() => {
                  this.setState({
                    unstakeNum: this.state.stakeBalance
                  })
                }}>MAX</span>} />

                <br></br>
                <br></br>
                <Button loading={this.state.stakeBtnLoading} disabled={!parseFloat(this.state.unstakeNum)} size="large" type="primary" style={{ width: '100%' }} onClick={async () => {
                  const loading = Wallet.loading();
                  this.setState({
                    stakeBtnLoading: true
                  });

                  const closeLoading = () => {
                    this.setState({
                      stakeBtnLoading: false
                    });
                    loading.close();
                  }
                  const result = await Wallet.unstakeNum(this.state.unstakeNum, () => {
                    message.success('');
                    this.initData();
                  });
                  if (!result) {
                    closeLoading();
                    return;
                  }

                  this.initData();
                  closeLoading();


                }}></Button>
              </Card>
            </Col>
            <Col span={6}>
              <Card title={"" + parseFloat(this.state.rewardEarned).toFixed(2)} >
                <br></br>
                <Button loading={this.state.rewardBtnLoading} size="large" type="primary" style={{ width: '100%' }} onClick={async () => {
                  const loading = Wallet.loading();
                  this.setState({
                    rewardBtnLoading: true
                  });

                  const closeLoading = () => {
                    this.setState({
                      rewardBtnLoading: false
                    });
                    loading.close();
                  }
                  const result = await Wallet.getReward(() => {
                    message.success('');
                    this.initData();
                  });
                  if (!result) {
                    closeLoading();
                    return;
                  }

                  this.initData();
                  closeLoading();


                }}></Button>
                <InputNumber size="large" style={{ opacity: 0 }} />
                <br></br>
              </Card>

            </Col>

            <Col span={6}>
              <Card title="" >
                <br></br>
                <Button loading={this.state.exitBtnLoading} size="large" type="primary" style={{ width: '100%' }} onClick={async () => {
                  const loading = Wallet.loading();
                  this.setState({
                    exitBtnLoading: true
                  });

                  const closeLoading = () => {
                    this.setState({
                      exitBtnLoading: false
                    });
                    loading.close();
                  }
                  const result = await Wallet.exit();
                  if (!result) {
                    closeLoading();
                    return;
                  }

                  this.initData();
                  closeLoading();


                }}></Button>
                <InputNumber size="large" style={{ opacity: 0 }} />
                <br></br>
              </Card>

            </Col>


          </Row>

        </div>
      </div>
    );;
  }
}


export default App;
