

import { Modal, message } from 'antd';
import { ethers, utils } from 'ethers';
import LPT_CONTRACT from '../contractsabi/qmtabi.json';
import STAKE_CONTRACT from '../contractsabi/stakeabi.json';
import RWT_CONTRACT from '../contractsabi/rwtabi.json';

const LPT_ADDRESS = '0x4fdbcC7331a03B1E323Ccd8968966282950ad769';
// const LPT_ADDRESS = '0x0687155715659e51ED9e6F67760f9D5B3Ad70DB3';
const STAKE_ADDRESS = '0xb8b2Dc7d62B4916872d9fB54D80afC396686568f';
// const STAKE_ADDRESS = '0x2e5f9f9fb98f3127a2b9fbe1146b2b95371b8f04';
// const STAKE_ADDRESS = '0x5f66e9De5756466299B7228fAC305B32BF9300ca';
const RWT_ADDRESS = '0x3710445d87B955BE147eF70aD57C91d20813133e';

let accountAddress;
export const modal = (params) => {
    return new Promise((res) => {
        Modal.confirm({
            ...params,
            onOk() {
                res();
            }

        });
    })
}


export const loading = (msg = 'Please wait') => {
    const hide = message.loading({
        content: msg,
        style: {
            width: "100%"
        }
    }, 0);
    return {
        close: hide
    };

}




export const testEthereum = () => {
    const { ethereum } = window;
    return typeof ethereum !== 'undefined';
}

export const getEthereum = () => {
    return window.ethereum;
}



export const connect = async () => {
    try {
        const ethereum = getEthereum();
        const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
        const address = accounts[0];
        accountAddress = address;
        return true;
    } catch (error) {
        return false;
    }


}


export const check = async () => {
    try {
        const ethereum = getEthereum();
        const accounts = await ethereum.request({ method: 'eth_accounts' });
        if (accounts.length === 0) {
            return;

        }

        accountAddress = accounts[0];
        return true;
    } catch (error) {
        return false;


    }
}

export const getChainId = async () => {
    const ethereum = getEthereum();
    const chainId = await ethereum.request({ method: 'eth_chainId' });
    return chainId;
}

export const getEthereumSigner = () => {
    const ethereum = getEthereum();
    const provider = new ethers.providers.Web3Provider(ethereum);
    const signer = provider.getSigner();
    return signer;
}



export const getLptContract = () => {
    const signer = getEthereumSigner();
    const contract = new ethers.Contract(LPT_ADDRESS, LPT_CONTRACT.abi, signer);
    return contract;
}


export const getStakeContract = () => {
    const signer = getEthereumSigner();
    const contract = new ethers.Contract(STAKE_ADDRESS, STAKE_CONTRACT.abi, signer);
    return contract;
}


export const getRWTContract = () => {
    const signer = getEthereumSigner();
    const contract = new ethers.Contract(RWT_ADDRESS, RWT_CONTRACT.abi, signer);
    return contract;
}


export const getLptBalance = async () => {
    try {

        const ctt = getLptContract();
        const num = await ctt.balanceOf(accountAddress);
        console.log(num)
        return utils.formatEther(num.toString())
    } catch (error) {
        console.log(error)

        return '0';
    }
}

export const testAllowanceLptNum = async () => {
    const num = utils.parseEther('100000000');
    const ctt = getLptContract();
    const allowanceNum = await ctt.allowance(accountAddress, STAKE_ADDRESS);
    return allowanceNum.gte(num);

}

export const approveLptNum = async (cb) => {
    const ctt = getLptContract();
    try {
        ctt.on('Approval', () => {
            cb();
        });

        await ctt.approve(STAKE_ADDRESS, utils.parseEther('10000000000'));
        return true;
    } catch (error) {

    }

    return false;

}


export const stakeLptNum = async (num, cb) => {
    const ctt = getStakeContract();
    ctt.on('Staked', () => {
        cb();

    })

    try {
        const result = await ctt.stake(utils.parseEther(num.toString()).toString());
        return true;
    } catch (error) {

    }
    return false;

}


export const getStakeBalance = async () => {
    try {
        const ctt = getStakeContract();
        const num = await ctt.balanceOf(accountAddress);
        return utils.formatEther(num.toString());
    } catch (error) {
        return '0';
    }
}
// 
export const getRewardEarned = async () => {
    try{
        const ctt = getStakeContract();
        const num = await ctt.earned(accountAddress);
        return utils.formatUnits(num.toString(),"6");//6
    } catch(error){
        return "0"
    }
}

export const unstakeNum = async (num, cb) => {
    try {
        const ctt = getStakeContract();
        ctt.on('Withdrawn', () => {
            cb();

        })

        const result = await ctt.withdraw(utils.parseEther(num.toString()).toString());
        return true;
    } catch (error) {
        return false;
    }

}


export const getReward = async (cb) => {
    try {
        const ctt = getStakeContract();
        ctt.on('RewardPaid', () => {
            cb();

        })
        const result = await ctt.getReward();
        return true;
    } catch (error) {
        return false;
    }
}

export const exit = async () => {
    try {
        const ctt = getStakeContract();
        const result = await ctt.exit();
        return true;
    } catch (error) {
        return false;
    }
}



